









































import Vue from 'vue'
import SingleAudio from '../components/SingleAudio.vue'
import { ITrackRecord } from '../interfaces'

export default Vue.extend({
	computed: {
		tracks(): ITrackRecord[]
		{
			return this.$store.state.tracks.slice(0, 10)
		},
		tracksLoaded(): boolean
		{
			return !!this.tracks.length
		}
	},
	components: {
		SingleAudio
	},
})
